/* @tailwind base; /* < Removing this did not disrupt any styles below and we are still able to use tailwind */
@tailwind components;
@tailwind utilities;

/* ------------------------------- Root */
.light-mode {
  --master-text-color: rgb(40, 40, 40);
  --master-text-color-low-op: rgba(0, 0, 0, 0.7);

  --JS-btn-hover: rgb(90, 90, 90);
  --JS-btn-color: black;
  --JS-btn-text-color: white;
  --JS-label-color: rgb(40, 40, 40);

  --faded-top: transparent;

  --slider-background-color: rgb(32, 136, 255);
  --slider-circle-color: rgb(250, 250, 250);
  --slider-circle-color-border: 0 0 5px 0 rgba(0, 0, 0, 0.2);
  --slider-circle-hover-color: rgb(255, 255, 255);
  --slider-background-hover-color: rgb(17, 99, 194);

  --genre-btn-color: black;
  --genre-btn-text-color: white;
  --genre-hover-btn-color: rgb(0, 86, 185);

  --main-btn-color: rgb(32, 136, 255);
  --main-btn-color-lowop: rgba(32, 136, 255, 0.5);
  --main-btn-lighter-hover-color: rgba(32, 136, 255, 0.7);

  --main-btn-hover-color: rgb(22, 98, 185);

  --main-btn-text-color: white;

  --main-delete-btn-color: rgb(255, 0, 0);
  --main-delete-btn-hover-color: rgb(211, 0, 0);
  --main-delete-text-color: rgb(255, 255, 255);

  --faq-btn-text-color: black;

  --routing-box-color: rgb(214, 214, 214);

  --Links-text-color: rgb(32, 136, 255);
  --Routing-Links-text-color: rgb(32, 136, 255);

  --input-background-color: white;
  --input-placeholder-text-color: black;
  --input-border: 1px solid black;

  --image-filter: grayscale(75%);

  --nav-background-color: rgba(177, 177, 177, 0.85);
  --card-background-color: rgb(255, 255, 255);
  --card-hover-color: rgb(245, 245, 245);

  --basic-text-color: rgb(0, 124, 31);
  --basic-background-color: rgba(148, 247, 173, 0.3);
  --value-text-color: rgb(0, 12, 124);
  --value-background-color: rgba(148, 158, 247, 0.3);
  --premium-text-color: rgb(124, 0, 48);
  --premium-background-color: rgba(119, 119, 119, 0.3);
  --plan-button: rgb(163, 163, 163);

  --HTML-background-color: rgb(235, 235, 235);

  --warning-bg-color: rgba(248, 132, 0, 0.856);
  --warning-hover-color: rgba(207, 111, 0, 0.856);
  --warning-text-color: black;
  --correct-bg-color: rgb(0, 255, 0);
  --correct-hover-color: rgb(0, 206, 0);
  --correct-text-color: black;
  --error-bg-color: red;
  --error-hover-color: rgb(197, 0, 0);
  --error-text-color: white;

  transition: 0.4s all;
}

.dark-mode {
  --master-text-color: rgb(224, 224, 224);
  --master-text-color-low-op: rgba(224, 224, 224, 0.7);

  --JS-btn-color: rgb(148, 28, 218);
  --JS-btn-hover: rgb(102, 14, 153);
  --JS-btn-text-color: rgb(216, 216, 216);
  --JS-label-color: rgb(224, 224, 224);

  --faded-top: rgba(0, 0, 0, 0.2);

  --slider-background-color: rgb(125, 19, 185);
  --slider-circle-color: rgb(192, 192, 192);
  --slider-circle-color-border: 0 0 5px 0 rgba(3, 2, 2, 0.2);
  --slider-circle-hover-color: white;

  --slider-background-hover-color: rgb(148, 28, 218);

  --tags-btn-background-color: rgb(68, 68, 68);

  --genre-btn-color: rgb(131, 131, 131);
  --genre-btn-text-color: rgb(255, 255, 255);
  --genre-hover-btn-color: rgb(88, 88, 88);

  --main-btn-color: rgb(148, 28, 218);
  --main-btn-color-lowop: rgba(148, 28, 218, 0.5);
  --main-btn-lighter-hover-color: rgba(148, 28, 218, 0.7);
  --main-btn-hover-color: rgb(102, 14, 153);
  --main-btn-text-color: rgb(216, 216, 216);

  --Links-text-color: rgb(148, 28, 218);
  --Routing-Links-text-color: rgb(201, 85, 255);

  --main-delete-btn-color: rgb(179, 53, 53);
  --main-delete-btn-hover-color: rgb(99, 26, 26);
  --main-delete-text-color: rgb(228, 228, 228);

  --faq-btn-text-color: white;

  --routing-box-color: rgb(101, 101, 101);

  --input-background-color: rgb(68, 68, 68);
  --input-placeholder-text-color: white;
  --input-border: 0.5px solid white;

  --image-filter: grayscale(75%);

  --nav-background-color: rgba(68, 68, 68, 0.85);
  --card-background-color: rgb(68, 68, 68);
  --card-hover-color: rgb(56, 56, 56);

  --basic-text-color: rgb(0, 255, 0);
  --basic-background-color: rgba(0, 124, 31, 0.527);
  --value-text-color: rgb(0, 26, 255);
  --value-background-color: rgba(148, 158, 247, 0.527);
  --premium-text-color: rgb(255, 0, 98);
  --premium-background-color: rgba(119, 119, 119, 0.527);
  --plan-button: rgb(223, 223, 223);

  --HTML-background-color: rgb(29, 29, 29);

  --warning-bg-color: rgba(63, 34, 0, 0.856);
  --warning-hover-color: rgba(100, 53, 0, 0.856);
  --warning-text-color: rgb(255, 123, 0);
  --correct-bg-color: rgb(0, 73, 0);
  --correct-hover-color: rgb(0, 121, 0);

  --correct-text-color: rgb(0, 255, 0);
  --error-bg-color: rgb(99, 0, 0);
  --error-hover-color: rgb(139, 0, 0);
  --error-text-color: rgb(255, 0, 0);

  transition: 0.4s all;
}

body {
  background-color: var(--HTML-background-color);
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  height: 200vh;
}

hr {
  width: 100px;
  border-color: white;
  opacity: 0.7;
}

input {
  color: var(--master-text-color);
}

button {
  user-select: none;
}

/* ------------------------------- Animations */
@keyframes popup {
  0% {
    opacity: 0;
    transform: translateX(-30px);
  }
  25% {
    opacity: 1;
    transform: translateX(-0);
  }

  75% {
    opacity: 1;
    transform: translateX(-0);
  }

  100% {
    opacity: 0;
    transform: translateX(35px);
  }
}
.animation-popup {
  animation-name: popup;
  animation-duration: 3.3s;
}

@keyframes revealComponent {
  0% {
    opacity: 0;
    transform: translateY(-40px);
  }

  100% {
    opacity: 1;
  }
}

.animation-revealComponent {
  animation-name: revealComponent;
  animation-duration: 0.5s;
}

@keyframes arrowLeft {
  0% {
  }

  50% {
    transform: translateX(-5px);
  }

  100% {
    opacity: 0;
    transform: translateX(-5px);
  }
}

@keyframes arrowRight {
  0% {
  }

  50% {
    transform: translateX(5px);
  }

  100% {
    opacity: 0;
    transform: translateX(5px);
  }
}

/* ------------------------------- Notification Component */

.notification-main {
  position: fixed;
  z-index: 5;
  top: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.single-popup {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 50px;
  width: 400px;
  margin: 5px 0;
  font-weight: 400;
  padding-left: 15px;
  box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.1);
}

.single-popup.error {
  display: none;
}
.single-popup.error.active {
  display: flex;
  background-color: var(--card-background-color);
  border-left: 8px solid red;
  color: red;
}

.single-popup.correct {
  display: none;
}
.single-popup.correct.active {
  display: flex;
  background-color: var(--card-background-color);
  border-left: 8px solid rgb(0, 233, 0);
  color: rgb(0, 233, 0);
}
.single-popup.warning {
  display: none;
}
.single-popup.warning.active {
  display: flex;
  background-color: var(--card-background-color);
  border-left: 8px solid rgba(248, 132, 0, 0.856);
  color: rgba(248, 132, 0, 0.856);
}

@media (max-width: 640px) {
  .single-popup {
    width: 250px;
  }
}

/* -------- Web Dev Project Images */

.proj-img {
  transform: scale(1);
  /* filter: var(--image-filter); */
  transition: all 0.2s ease-in-out 25ms;
}

.proj-img:hover {
  transform: scale(1.05);
  filter: grayscale(0%) brightness(100%);
  transition: all 0.2s ease-in-out 25ms;
}

/* -------- Trigger Component */
.triggers {
  margin: 40px 0;
}

.error {
  background-color: var(--error-bg-color);
  color: var(--error-text-color);
  transition: all 0.2s ease-in-out;
}
.correct {
  background-color: var(--correct-bg-color);
  color: var(--correct-text-color);
  transition: all 0.2s ease-in-out;
}
.warning {
  background-color: var(--warning-bg-color);
  color: var(--warning-text-color);
  transition: all 0.2s ease-in-out;
}
.error:hover {
  background-color: var(--error-hover-color);
  transition: all 0.2s ease-in-out;
}
.error:active {
  transform: scale(0.95);
}
.correct:hover {
  background-color: var(--correct-hover-color);
  transition: all 0.2s ease-in-out;
}
.correct:active {
  transform: scale(0.95);
}
.warning:hover {
  background-color: var(--warning-hover-color);
  transition: all 0.2s ease-in-out;
}
.warning:active {
  transform: scale(0.95);
}
.triggers button {
  padding: 7px 20px;
  margin: 0 10px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

@media (min-width: 330px) {
  .triggers {
    margin: 25px 0;
  }

  .triggers button {
    margin: 7px 0;
  }
}

@media (min-width: 640px) {
  .triggers {
    margin: 40px 0;
  }

  .triggers button {
    padding: 7px 20px;
    margin: 0 10px;
  }
}
/* ------------------------------- Title/Notes Component */

.title {
  display: flex;
  align-items: center;
  margin-top: 35px;
  color: var(--JS-label-color);
}

.title p,
.title h4 {
  margin: 0;
}

.title h4 {
  margin-bottom: 10px;
}

.title p {
  margin-left: 5px;
}

.notes {
  display: flex;
  align-items: center;
  margin-top: 35px;
  transition: all 1s ease;
}

.notes p,
.notes h4 {
  margin: 0;
  color: red;
}

.notes h4 {
  text-transform: uppercase;
}
.notes p {
  margin-left: 5px;
}

/* ------------------------------- Nav */
.nav-icon {
  pointer-events: all;
  cursor: pointer;
  background-color: var(--card-background-color);
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.05);
  border-radius: 5px;
  padding: 3px;
  transition: 0.2s all;
}

.nav-main {
  position: relative;
  z-index: 3;
  pointer-events: none;
  position: fixed;
  width: 30px;
  height: 30px;
  top: 20px;
  transition: 0.4s all ease-in-out;
}

.nav-main.active {
  pointer-events: all;
  width: 30px;
  height: 30px;
  left: 250px;
  z-index: 3;
  transition: 0.5s all ease-in-out;
}

.nav-body .toggle-btn {
  margin: 0;
}
.nav-body {
  opacity: 0;
  display: none;
  position: relative;
  z-index: 3;
  left: -230px;
  display: flex;
  justify-content: center;
  height: 1000px;
  background-color: var(--nav-background-color);
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
  transition: 0.6s opacity ease;
  pointer-events: none;
  backdrop-filter: blur(5px);
}

.nav-body hr {
  margin: 20px auto;
}
.nav-body ul {
  list-style: none;
  padding: 0;
}

.nav-body ul li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.nav-body button {
  border: none;
  margin: 4px 0;
  padding: 10px 0;
  font-weight: 800;
  background-color: var(--main-btn-color);
  color: var(--main-btn-text-color);
  cursor: pointer;
  transition: 0.3s all ease;
}
.nav-body button:hover {
  background-color: var(--main-btn-hover-color);
  color: var(--main-btn-text-color);
}

.nav-body.active {
  opacity: 1;
  position: relative;
  z-index: 3;
  transition: 0.7s all ease-in-out;
  pointer-events: unset;
}

.nav-icon:hover {
  background-color: var(--card-hover-color);
}

.nav-body p {
  font-size: 15px;
  display: flex;
  justify-content: center;
  color: var(--main-btn-text-color);
  font-weight: 800;
  margin-top: 0;
}

@media (max-width: 640px) {
  .nav-main {
    left: 60px;
  }
  .nav-icon {
    position: relative;
    left: -30px;
    top: 5px;
  }

  .nav-body {
    top: -80px;
  }
}

@media (min-width: 640px) {
  .nav-main {
    left: -80px;
    top: 38px;
  }
  .nav-icon {
    position: relative;
    left: 130px;
  }

  .nav-body {
    top: -88px;
  }
}

@media (min-width: 1025px) {
  .nav-main {
    left: 30px;
    top: 40px;
  }
  .nav-icon {
    position: relative;
    left: 34px;
  }
  .nav-body {
    top: -95px;
  }
}
/* ------------------------------- Passsword Strength */
.passComp {
  margin-top: 40px;
  display: flex;
  justify-content: center;
}

.password-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 360px;
  border-radius: 10px;
  background-color: var(--card-background-color);
  transition: all 0.5s ease-in-out;
  box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.15);
}

.password-main form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.pass-card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.pass-card p {
  margin: 0;
  text-align: center;
  font-size: 14px;
  margin-top: 15px;
}
.password-main h4 {
  color: var(--master-text-color);
  margin: 0;
  margin-bottom: 10px;
  transition: all 0.5s ease-in-out;
}

.password-main button {
  color: white;
  background: var(--main-btn-color);
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}

.password-main button:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

.pass-card button:hover {
  background-color: var(--main-btn-hover-color);
}

.pass-card .inputField {
  margin: 0;
  margin-top: 10px;
}

.pass-field {
  position: relative;
}

.reveal-password {
  position: absolute;
  right: 10px;
  top: 21px;
  color: var(--master-text-color);
  transition: all 0.5s ease-in-out;
  cursor: pointer;
}
.pass-bar {
  overflow: hidden;
  width: 182px;
  margin-top: 10px;
  height: 5px;
  border-radius: 10px;
  background-color: rgb(189, 189, 189);
}

.pass-fill {
  width: 0;
  transition: 0.5s all ease-in-out;
}
.pass-fill.mild {
  width: 25%;
  height: 6px;
  background-color: rgb(0, 190, 0);
  transition: 0.5s width ease-in-out;
}

.pass-fill.medium {
  width: 50%;
  height: 6px;
  background-color: rgb(0, 190, 0);
  transition: 0.5s width ease-in-out;
}

.pass-fill.stronger {
  width: 75%;
  height: 6px;
  background-color: rgb(0, 190, 0);
  transition: 0.5s width ease-in-out;
}

.pass-fill.strongest {
  width: 100%;
  height: 6px;
  background-color: rgb(0, 190, 0);
  transition: 0.5s width ease-in-out;
}

.pass-status {
  display: grid;
  grid-template: 1fr 1fr / 1fr 1fr;
  margin: 8px 0;
}
.passCircle {
  width: 10px;
  height: 10px;
  border-radius: 50px;
}
.passCircle.default {
  background-color: red;
}
.passCircle.active {
  background-color: green;
}

.single-stat {
  margin: 5px 0;
  display: flex;
  align-items: center;
}

.single-stat p {
  margin: 0;
  padding: 0;
  font-size: 14px;
  margin-left: 10px;
}

.passStat {
  color: rgb(0, 0, 0);
}
.passStat.mild {
  color: red;
}
.passStat.medium {
  color: rgb(255, 136, 0);
}
.passStat.strong {
  color: green;
}

@media (max-width: 640px) {
  .single-stat p {
    font-size: 11.5px;
  }

  .passCircle {
    width: 7px;
    height: 7px;
  }
}
/* ------------------------------- Nav bar */

.faded-bar {
  height: 100px;
  width: 120%;
  position: fixed;
  /* z-index: 1; */
  background: linear-gradient(to bottom, var(--faded-top), transparent);
  pointer-events: none;
  transition: all 0.5s ease-in-out;
}

/* ------------------------------- MAIN JS File */

.mainLinks a {
  color: var(--Links-text-color);
}
.mainJS {
  position: relative;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--master-text-color);
  transition: all 0.5s ease-in-out;
}

.mainJS.nav-active {
  left: 90px;
  transition: all 0.5s ease-in-out;
}

.jsBtn {
  padding: 10px 15px;
  border-radius: 5px;
  border: none;
  background-color: var(--JS-btn-color);
  color: var(--JS-btn-text-color);
  font-weight: 800;
  margin-top: 35px;
  margin-bottom: 35px;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}

.jsBtn:hover {
  background-color: var(--JS-btn-hover);
  color: var(--JS-btn-text-color);
  transform: scale(0.98);
  transition: all 0.1s ease-in-out;
}

.jsBtn:active {
  transform: translateY(2px) scale(0.98);
  transition: all 0.1s ease-in-out;
}

.topBtn {
  position: fixed;
  bottom: 80px;
  /* right: 100px; */
  opacity: 0;
  color: var(--JS-btn-color);
  cursor: pointer;
  border-radius: 100px;
  background-color: white;
  transition: all 0.5s ease-in-out;
}

.topBtn.active {
  opacity: 1;
  position: fixed;
  /* right: 100px; */
  bottom: 100px;
  color: var(--JS-btn-color);
  cursor: pointer;
  border-radius: 100px;
  background-color: white;
  transition: all 0.5s ease-in-out;
}

@media (min-width: 1025px) {
  .mainLinks div {
    margin: 0px 20px;
  }
  .mainLinks {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .topBtn {
    right: 100px;
  }
  .topBtn.active {
    right: 100px;
  }
  .jsBtn {
    font-size: 16px;
    padding: 11px 16px;
  }
}

@media (max-width: 1025px) {
  .mainLinks {
    display: grid;
    grid-template: 1fr 1fr / 1fr 1fr;
  }
  .mainLinks div {
    margin: 25px 35px;
  }

  .topBtn {
    right: 30px;
    bottom: 60px;
  }
  .topBtn.active {
    right: 30px;
    bottom: 60px;
  }

  .jsBtn {
    font-size: 15px;
    padding: 10px 15px;
  }
}

@media (max-width: 640px) {
  .jsBtn {
    font-size: 14px;
    padding: 8px 13px;
  }

  .mainLinks div {
    margin: 15px 25px;
  }
}

/* ------------------------------- Toggle Button*/

.toggle-btn {
  position: relative;
  margin-top: 25px;
  width: 60px;
  height: 30px;
  border-radius: 100px;
  background-color: var(--slider-background-color);
  margin-bottom: 35px;
  transition: all 0.5s ease-in-out;
}

.toggle-btn:hover {
  background-color: var(--slider-background-hover-color);
  transition: all 0.5s ease-in-out;
}

.mode-icons img {
  position: absolute;
  z-index: 0;
  top: 4px;
  width: 22px;
  height: 22px;
  cursor: pointer;
}

.light {
  left: 7px;
}

.dark {
  right: 5px;
}
.toggle-circle {
  position: relative;
  z-index: 1;
  transform: translateX(0);
  width: 30px;
  height: 30px;
  border-radius: 50px;
  background-color: var(--slider-circle-color);
  transition: 0.5s all cubic-bezier(0.47, 0, 0.51, 1);
  cursor: pointer;
}

.toggle-circle.active {
  transform: translateX(35px);
  transition: 0.5s all cubic-bezier(0.47, 0, 0.51, 1);
}

/* ------------------------------- Pagination */
.pagination-container {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}
.paginationBtn {
  cursor: pointer;
  padding: 0;
  margin: 5px 0;
  list-style: none;
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.paginationBtn a {
  padding: 2px 8px;
  margin: 2px;
  border-radius: 5px;
  border: 1px solid var(--master-text-color-low-op);
  color: var(--master-text-color);
  transition: 0.2s all cubic-bezier(0.47, 0, 0.51, 1);
}

.paginationBtn a:hover {
  color: white;
  background-color: var(--main-btn-color);
  transition: 0.2s all cubic-bezier(0.47, 0, 0.51, 1);
}

.paginationActive a {
  color: white;
  background-color: var(--main-btn-color);
  transition: 0.2s all cubic-bezier(0.47, 0, 0.51, 1);
}

.paginationDisabled {
  opacity: 0.3;
  cursor: not-allowed;
}

/* Show item count */
.dropbtn {
  background-color: var(--main-btn-color);
