  width: 100px;
  height: 30px;
  border-radius: 5px;
  background-color: var(--basic-background-color);
  margin: 0;
  transition: 0.5s all ease-in-out;
}

.value {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--value-text-color);
  width: 100px;
  height: 30px;
  border-radius: 5px;
  background-color: var(--value-background-color);
  margin: 0;
  transition: 0.5s all ease-in-out;
}

.premium {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--premium-text-color);
  width: 125px;
  height: 30px;
  border-radius: 5px;
  background-color: var(--premium-background-color);
  margin: 0;
  transition: 0.5s all ease-in-out;
}
.reset-plans {
  /* reset button*/
  margin-bottom: 10px;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  margin: 1px 5px;
  cursor: pointer;
  transition: all 0.2s ease;
  background-color: var(--main-btn-color);
  color: var(--main-btn-text-color);
}

.expandCollapse button:important {
  position: absolute;
}

.delete-plan {
  position: absolute;
  border: none;
  border-radius: 5px;
  color: var(--main-delete-text-color);
  background-color: var(--main-delete-btn-color);
  font-weight: 800;
  padding: 3px 10px;
  margin: 2px 0;
  cursor: pointer;
  transform: translateX(25px) translateY(10px);
  transition: all 0.5s ease-in-out;
}
.delete-plan:hover {
  background-color: var(--main-delete-btn-hover-color);
  transition: all 0.2s ease;
}

.delete-plan:active {
  transition: all 0.2s ease;
}
/* ------------------------------- FAQ section */

.Cards {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 15px;
}

.inactive {
  display: none;
}
.single-card {
  /* FAQ prompt */
  position: relative;
  background-color: var(--card-background-color);
  border-radius: 10px;
  width: 400px;
  cursor: pointer;
  margin: 10px 0;
  box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.05);
  transition: all 0.5s ease-in-out;
}

.single-card:hover {
  transform: scale(1.03);
  transition: 0.2s all;
}

.single-card:active {
  transform: scale(1);
}

.answer {
  display: none;
}

.answer.active {
  display: flex;
}

.single-card h4,
.single-card p {
  padding: 15px;
}

.single-card p,
.single-card h4 {
  margin: 0;
  color: var(--faq-btn-text-color);
  transition: all 0.5s ease-in-out;
}

.single-card i {
  color: red;
}

.addIcon {
  position: absolute;
  right: 15px;
  top: 35%;
  transition: 0.2s transform ease-in-out;
}

.addIcon.active {
  position: absolute;
  right: 15px;
  top: 20%;
  transform: rotate(45deg);
  transition: 0.2s transform ease-in-out;
}

