/* ------------------------------- Tabs */

.allTabs {
  position: relative;
  background-color: var(--card-background-color);
  border-radius: 10px;
  width: 400px;
  height: 275px;
  box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.15);
  display: flex;
  align-items: flex-start;
  justify-content: center;
  transition: all 0.9s;
  margin-bottom: 45px;
}

.Tab-section {
  display: flex;
}

.tab-content {
  display: none;
}

.tab-content.active {
  display: unset;
  position: absolute;
  width: 80%;
  left: 40px;
}

/* .tab-content.active p {
  font-size: 14px;
} */
.single-tab {
  margin: 0 20px;
}

.single-tab .inactive-title hr {
  width: 5px;
  border-radius: 50px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  margin-bottom: 0;
  transition: all 0.5s ease-in-out;
}

.single-tab .active-title hr {
  display: flex;
  margin-bottom: 0;
  width: 45px;
  border: 1px solid #0e86d4;
  border-radius: 5px;
  transition: all 0.5s ease-in-out;
}

.single-tab h4 {
  margin-bottom: 0;
}

.tab-content h4 {
  margin-top: 15px;
}

.tabBtn {
  color: white;
  background: var(--main-btn-color);
  border: none;
  padding: 10px 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: all 1s ease;
}

.tabBtn:hover {
  background: var(--main-btn-hover-color);
  transition: all 0.1s ease;
}
.tabBtn:active {
  transform: scale(0.98);
  transition: all 0.1s ease;
}

.tabInput {
  background: var(--input-background-color);
  border: var(--input-border);
  padding: 10px 15px;
  border-radius: 5px;
  transition: all 0.5s ease-in-out;
}

.readMore {
  color: rgb(0, 128, 255);
  border: none;
  background: none;
  cursor: pointer;
}
.tab-title {
  cursor: pointer;
}

.routing-about {
  overflow: scroll;
}

@media (max-width: 640px) {
  .allTabs {
    width: 300px;
  }

  .tab-title {
    font-size: 17px;
  }

  .single-tab .active-title hr {
    width: 30px;
  }

  .single-tab {
    margin: 0 14px;
  }

  .tab-content.active {
    left: 34px;
  }

  .readMore {
    margin-top: 5px;
    padding: 0;
  }
}
/* ------------------------------- Feed back component */

.radio-toolbar {
  margin: 12px 0;
}
/*To styilze our radio buttons, we must first hide the original styilng*/
.radio-toolbar input[type='radio'] {
  opacity: 0;
  position: fixed;
  width: 0;
}
.radio-toolbar label {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: var(--main-btn-color-lowop);
  width: 35px;
  height: 35px;
  font-family: sans-serif, Arial;
  font-size: 18px;
  margin: 0 5px;
  border-radius: 350px;
  transition: all 0.3s ease-in-out;
}

.radio-toolbar input[type='radio']:checked + label {
  background-color: var(--main-btn-color);
}

.radio-toolbar label:hover {
  background-color: var(--main-btn-hover-color);
}
.radio-toolbar li {
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.feedbackForm textarea {
  background: var(--input-background-color);
  border: var(--input-border);
  color: var(--master-text-color);
  border-radius: 5px;
  width: 300px;
  height: 90px;
  font-family: inherit;
  resize: none;
  transition: all 0.5s ease-in-out;
}

@media (max-width: 640px) {
  .feedbackForm textarea {
    width: 225px;
  }
}

.feedbackForm {
  background-color: var(--card-background-color);
  box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.15);
  height: 360px;
  transition: all 0.9s;
}

.feedbackForm h3 {
  font-size: 20px;
}
.feedback-reset {
  height: 40px;
}

.feedbackForm button {
  color: var(--main-btn-text-color);
  background: var(--main-btn-color);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.5s ease;
}

.feedbackForm button:hover {
  background-color: var(--main-btn-lighter-hover-color);
  color: var(--main-btn-text-color);
}

.feedbackForm button:disabled {
  cursor: not-allowed;
  background-color: var(--main-btn-color-lowop);
}

.feedback-result p {
  margin: 10px 0;
}
